import { Component, OnInit } from '@angular/core';
import { ApiserviceService } from '../apiservice.service'

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  i:any={}
  j:number=0
  constructor(private apiservice : ApiserviceService) {  
  }
 
  ngOnInit() {
    //this is the hiding mechanism 
    if(JSON.parse(sessionStorage.getItem('user')) == null){
     console.log("in if ")
    }else{
      console.log("in else calculating cart value")
      var user = JSON.parse(sessionStorage.getItem('user'))
       var name=user.fname
       this.apiservice.getCartcount(user.id).subscribe((i)=>{
        console.log("in service call getCartcount = ")
        this.j=i.count
        console.log("j=")
        console.log(this.j)
       document.getElementById("carttext").innerText= "Cart :"+this.j + " item(s)";
       this.apiservice.getWishcount(user.id).subscribe((i)=>{
        console.log("in service call getCartcount = ")
        this.j=i.count
        console.log("j=")
        console.log(this.j)
       document.getElementById("wishtext").innerText= "WishList :"+this.j + " item(s)";
        })
        })
      }

    console.log("session_name = ")
    console.log(name)
    if(name==undefined){
      console.log("header in if")
      document.getElementById("logout").style.display= "none";
      document.getElementById("cart").style.display= "none";
      document.getElementById("wishlist").style.display= "none";
      document.getElementById("sell").style.display= "none";
    }else{

      console.log("header in else")
      document.getElementById("login").style.display= "none";
      document.getElementById("signup").style.display= "none";
    }
  }

}